// Link styles not addressed in Bootstrap
a {
  border-bottom: 1px dotted currentColor;
  overflow: visible;
  text-decoration: none;
  transition: $transition;

  @include hover-focus {
    border-bottom: 0;
  }
  
  &:focus {
    outline: 2px solid $mp-primary;
    outline-offset: 0;
  }
}

// Responsive and fluid type sizing
// Root (mobile) sizes are set in abstracts/variables-bs4/bs4-typography.scss
.display-1 {
  @include one-element-size(display-1);
  //color: $policy-blue;
}

//.display-2 { @include one-element-size(display-2); }

//.display-3 { @include one-element-size(display-3); }

.display-4 {
  @include one-element-size(display-4);
}


// Headings
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  color: $mp-primary;
}


h1, .h1 {
  @include one-element-size(h1);
}

h2, .h2 {
  @include one-element-size(h2);
}

h3, .h3 {
  @include one-element-size(h3);
}

h4, .h4 {
  @include one-element-size(h4);
}

h5, .h5,
.lead {
  @include one-element-size(h5);
}

h6, .h6 {
  @include one-element-size(h6);
}

// Other block level elements
p,
li,
address,
label,
.form-control,
.btn {
  @include one-element-size('base', false);
}

